import React, { FC } from 'react';
import Container from './container';
import Subtitle from './subtitle';
import SelectMenu from './select';
import SwitchSettings from './switch';

const PreferencesPersonaAreaPage: FC = () => {

  return (
    <Container>
      <Subtitle subtitle='Global Application Settings' />
      <SelectMenu subtitle='Language' />
      <Subtitle subtitle='Vault Password Manager' />
      <SwitchSettings
        nameSetting='Vault chrome extension'
        tooltipTxt='Vault Tooltip'
      />
      <Subtitle
        subtitle='Browser Profile Default Settings'
      />
      <SwitchSettings
        switchList={true}
        nameSetting='Enable password settings'
        tooltipTxt='Password Tooltip'
      />
      <SwitchSettings
        switchList={true}
        nameSetting='Enable browser bookmark saving'
        tooltipTxt='Bookmark Tooltip'
      />
      <SwitchSettings
        switchList={true}
        nameSetting='Enable browser history saving'
        tooltipTxt='Browser history saving Tooltip'
      />
      <SelectMenu
        selectList={true}
        subtitle='My native screen resolution'
        tooltipTxt='Tooltip native screen'
      />
      <SelectMenu
        subtitle='Minimal screen resolution'
        tooltipTxt='Minimal screen tooltip'
      />
    </Container>
  )
}

export default PreferencesPersonaAreaPage;
