import styled from '@emotion/styled';
import { Menu } from 'antd';
import React, { FC } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

import { MenuKey } from '../menu-key';

const isElectron = !!window.require;

const BetaBlock = styled.span`
  background-color: red;
  color: var(--FFFFFF-personal-area-menu);
  margin-left: 5px;
  padding: 1px 3px;
  font-size: 10px;
  border-radius: 2px;
`;

declare interface IPersonalAreaMenu {
  selectedKey: MenuKey;
}
const PersonalAreaMenu: FC<IPersonalAreaMenu> = (props) => {
  const { t: translation } = useTranslation();

  return (
    <>
      <Menu
        mode='inline'
        style={{ width: 240 }}
        defaultSelectedKeys={[props.selectedKey]}
      >
        <Menu.Item key='overview'>
          <Link to='/personalArea'>{translation('personalArea.overview.title')}</Link>
        </Menu.Item>
        <Menu.Item key='default-profile-settings'>
          <Link to='/personalArea/default-profile-settings'>
            {translation('personalArea.defaultProfile.title')}
          </Link>
        </Menu.Item>
        <Menu.Item key='billing'>
          <Link to='/personalArea/Billing'>{translation('personalArea.billing.title')}</Link>
        </Menu.Item>
        <Menu.Item key='two-factor-auth'>
          <Link to='/personalArea/2fa'>{translation('personalArea.twoFA.title')}</Link>
        </Menu.Item>
        {/*<Menu.Item key='preferences'>*/}
        {/*  <Link to='/personalArea/Preferences'>Preferences</Link>*/}
        {/*</Menu.Item>*/}
        {/* <Menu.Item key='password'>
        <Link to='/Password'>Password</Link>
        </Menu.Item> */}
        {/*<Menu.Item key='payment-method'>*/}
        {/*<Link to='/personalArea/PaymentMethod'>Payment Method</Link>*/}
        {/*</Menu.Item>*/}
        {/* <Menu.Item key='payment-history'>
        <Link to='/PaymentHistory'>Payment History</Link>
        </Menu.Item> */}
        <Menu.Item key='affiliateProgram'>
          <Link to='/personalArea/AffiliateProgram'>{translation('personalArea.affiliateProgram.title')}</Link>
        </Menu.Item>
        <Menu.Item key='active-sessions'>
          <Link to='/personalArea/ActiveSessions'>{translation('personalArea.activeSessions.title')}</Link>
        </Menu.Item>
        <Menu.Item key='token-api'>
          <Link to='/personalArea/TokenApi'>{translation('personalArea.api.title')}</Link>
        </Menu.Item>
        {isElectron &&
        (
          <Menu.Item key='cache'>
            <Link to='/personalArea/Cache'>{translation('personalArea.cache.title')}</Link>
          </Menu.Item>
        )}
        <Menu.Item key='password-manager'>
          <Link to='/personalArea/PasswordManager'>{translation('personalArea.vault.title')}</Link>
        </Menu.Item>
        {/* <Menu.Item key='preferences'>
        <Link to='/Preferences'>Preferences</Link>
        </Menu.Item> */}
      </Menu>
    </>
  );
};

export default PersonalAreaMenu;
