import React, { FunctionComponent } from 'react';
import styled from '@emotion/styled';
import { Divider } from 'antd';

declare interface ISubtitleProps {
  subtitle: string;
}

const Container = styled('div')`
  width: 322px;
  margin-top: 35px;
  margin-bottom: 22px;
  display: flex;
  flex-direction: column;
`;

const SubtitleTxt = styled('div')`
  line-height: 19px;
  font-size: 16px;
  color: var(--474747);
`;

const DividerCustom = styled(Divider)`
  margin-top: 5px;
  margin-bottom: 0px;
`;

const Subtitle: FunctionComponent<ISubtitleProps> = ({ subtitle }) => (
  <Container>
    <SubtitleTxt>{subtitle}</SubtitleTxt>
    <DividerCustom />
  </Container>
);

export default Subtitle;
