import styled from '@emotion/styled';
import { Layout } from 'antd';
import React, { FunctionComponent } from 'react';
import { useTranslation } from 'react-i18next';

import Header from '../../../../ui/Header';
import BodyContainer from '../../components/body-container';
import MenuPersonalArea from '../../menu/components/menu';
import { NEW_FEATURES } from '../../../../../../feature-toggle';

const Subtitle = styled('div')`
  font-family: Roboto;
  font-size: 20px;
  line-height: 19px;
  color: var(--000000);
  opacity: 0.8;
  padding-top: 40px;
`;

const Row = styled('div')`
  display: flex;
`;

const MainContainer = styled(Layout)`
  overflow: hidden;
`;

const BlockShadow = styled('div')`
  box-shadow: 0px 0px 10px var(--E5E5E766);
  width: 100%;
  min-height: 100vh;
`;

const ContainerContent = styled('div')`
  margin: 0 86px;
  color: var(--000000);
  font-family: Roboto;
  font-size: 12px;
  display: flex;
  justify-content: space-between;
`;

const PageTitle = styled('span')`
  margin-top: 40px;
  margin-bottom: 47px;
  color: var(--000000);
  font-family: Roboto;
  font-size: 20px;
  line-height: 23px;
  display: inline-block;
`;

const Col = styled('div')`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

const ContentTabs = styled('div')`
  font-size: 14px;
  line-height: 16px;
  opacity: 0.8
`;

const ContentPage = styled('div')`
  display: flex;
  flex-direction: column;
  margin-left: 69px;
`;

const Container: FunctionComponent = ({ children }) => {
  const { t: translation } = useTranslation();

  return (
    <>
      <MainContainer>
        <Header />
        <BodyContainer>
          <ContainerContent>
            <Col>
              <ContentTabs>
                <Row>
                  <div>
                    <PageTitle>
                      {translation('personalArea.title')}
                    </PageTitle>
                    <MenuPersonalArea selectedKey='preferences' />
                  </div>
                  <BlockShadow>
                    <Subtitle style={{ marginLeft: 69 }}>
                      {translation('personalArea.preferences')}
                    </Subtitle>
                    <ContentPage>{children}</ContentPage>
                  </BlockShadow>
                </Row>
              </ContentTabs>
            </Col>
          </ContainerContent>
        </BodyContainer>
      </MainContainer>
    </>
  );
};

export default Container;
